import { register, officialWebsite } from '../../utils/api'
export default {
  data() {
    return {
      codeText: '',
      num: 60,
      name: "",
      name_error: "",
      company_name: "",
      company_name_error: "",
      phone: "",
      phone_error: "",
      code: "",
      code_error: "",
      clearFixed: false,
      hidePlan: false,
      showForm: false
    };
  },
  mounted() {
    let footer_height = 290;
    if(window.innerWidth < 641) {
      footer_height = 400;
    }
    const timer = setTimeout(() => {
      const documentHeight = document.body.scrollHeight;
      document.addEventListener("scroll", () => {
        const scroll =
          document.documentElement.scrollTop || document.body.scrollTop;
        let top = scroll + window.innerHeight + footer_height;
        if (top >= documentHeight && !this.clearFixed) {
          this.clearFixed = true;
        }
        if (top < documentHeight && this.clearFixed) {
          this.clearFixed = false;
        }
      });
      clearTimeout(timer);
    }, 2000);
  },
  methods: {
    getCode() {
      //获取验证码
      if(!this.phone){
        this.phone_error = "请填写您的联系电话";
        return;
      }
      if (!/^1([3-9])\d{9}$/.test(this.phone)) {
        this.phone_error = "请填写正确的联系电话";
        return;
      }
      register(this.$axios, {
        phone_number: this.phone,
        app_name: "橡树黑卡",
      }).then(res => {
        this.session_code = res.session_code;
        this.timer();
      });
    },
    getPlan() {
      if (!this.name) {
        this.name_error = "请填写您的姓名";
        return;
      } else {
        this.name_error = "";
      }
      if (!this.phone) {
        this.phone_error = "请填写您的联系电话";
        return;
      } else {
        if (!/^1([3-9])\d{9}$/.test(this.phone)) {
          this.phone_error = "请填写正确的联系电话";
          return;
        }
        this.phone_error = "";
      }
      if (!this.code) {
        this.code_error = "请填写验证码";
        return;
      } else {
        this.code_error = "";
      }
      officialWebsite(this.$axios, {
        name: this.name,
        phone_number: this.phone,
        company: this.company_name,
        otp: this.code,
        session_code: this.session_code
      }).then(() => {
        this.showDialog = true;
        this.phone = '';
        this.company_name = '';
        this.code = '';
        this.name = '';
      })
    },
    timer() {
      let time = setInterval(() => {
        this.num--;
        this.codeText = `${this.num}s后重新获取`
        if(this.num <= 0){
          clearInterval(time);
          this.codeText = '';
          this.num = 60;
        }
      }, 1000)
    }
  },
};
